
import { connect } from 'react-redux';
import { resourceTrigger } from '../actions/actionTriggers';
import { bindActionCreators } from 'redux';
import { EmployeeList } from '../../Components/admin/EmployeeList';

const mapStateToProps =(state) =>{
    return {resources: state.resourcedata.resources}
}

const mapActiontoProps = (dispatch) =>{
    return bindActionCreators({loadResources:resourceTrigger},dispatch)
}

export const EmployeeListConnected = connect(mapStateToProps,mapActiontoProps)(EmployeeList);